/* Общий контейнер для страницы */
.generator-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  text-align: center;
  border-radius: 10px;
}

/* Карточка генерации */
.generator-card {
  background-color: #fff;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
}

.card-header {
  text-align: center;
  margin-bottom: 20px;
}

.card-title {
  font-size: 2.2rem;
  font-weight: bold;
  color: #333;
  margin-bottom: 10px;
}

.card-description {
  color: #6c757d;
  font-size: 1.1rem;
}

/* Иконка в заголовке */
.icon-container {
  display: flex;
  justify-content: center;
  margin-bottom: 10px;
}

.icon {
  width: 50px;
  height: 50px;
  stroke: #000;
}

/* Содержимое формы */
.generator-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}

.form-group {
  width: 100%;
  margin-bottom: 20px;
  text-align: left;
}

.form-group label {
  display: block;
  font-weight: 600;
  margin-bottom: 8px;
  color: #333;
}

.form-group input {
  width: 100%;
  padding: 12px;
  font-size: 1rem;
  border: 1px solid #ddd;
  border-radius: 5px;
  transition: border 0.3s;
}

.form-group input:focus {
  border-color: #007bff;
  outline: none;
}

/* Кнопка генерации */
.generate-btn {
  background-color: #111;
  color: #fff;
  padding: 15px 30px;
  text-decoration: none;
  border-radius: 5px;
  font-size: 1em;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.2s;
}

.generate-btn:hover {
  background-color: #0056b3;
  transform: translateY(-2px);
}

/* Ошибки */
.error-message {
  color: #dc3545;
  font-size: 1rem;
  margin-top: 15px;
}

/* Карточка с результатом */
.result-card {
  background-color: #ffffff;
  padding: 1.5rem;
  margin-top: 2rem;
  border-radius: 0.5rem;
  text-align: left;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  border: 1px solid #e2e8f0;
  transition: all 0.3s ease;
}
.result-card:hover {
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

.result-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: #2d3748;
  margin-bottom: 1rem;
  border-bottom: 2px solid #e2e8f0;
  padding-bottom: 0.5rem;
}

.result-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1rem;
}

.result-item {
  display: flex;
  flex-direction: column;
  background-color: #f7fafc;
  padding: 1rem;
  border-radius: 0.375rem;
  transition: all 0.3s ease;
}
.result-item:hover {
  background-color: #edf2f7;
}

.result-label {
  font-weight: 600;
  color: #4a5568;
  margin-bottom: 0.25rem;
  font-size: 0.875rem;
}
.result-value {
  font-weight: 700;
  color: #2d3748;
  font-size: 1.125rem;
}


/* Кнопка "Назад на главную" */
.back-btn {
  margin-top: 20px;
  background-color: #111;
  color: #fff;
  padding: 15px 30px;
  text-decoration: none;
  border-radius: 5px;
  font-size: 1em;
  transition: background-color 0.3s ease;
}
.back-btn:hover {
  background-color: #007bff;
  color: #fff;
}
